
body, form {
  margin: 0;
  padding: 0;
}

.container {
  display: flex;
  justify-content: center;
  height: 60px;
  background-color: #236dbc;
  align-items: center;
  position: relative;
}

.search-form {
  display: flex;
  align-items: center;
  position: relative;
}

.search-form input {
  padding: 8px;
  border: none;
  border-radius: 5px;
  outline: none;
  font-size: 16px;
  width: 350px;
}

.search {
  display: flex;
  align-items: center;
}

.search-button {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  border: none;
  border-radius: 0 5px 5px 0;
  background-color: rgb(228, 213, 213);
  color: white;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

